.ant-modal {
  position: relative;
  width: auto;
  margin: 0 auto;
  top: 100px;
  padding-bottom: 24px;
}
.ant-modal-wrap {
  position: fixed;
  overflow: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  -webkit-overflow-scrolling: touch;
  outline: 0;
}
.ant-modal-title {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  font-weight: bold;
}
.ant-modal-content {
  position: relative;
  background-color: #fff;
  border: 0;
  border-radius: 6px;
  background-clip: padding-box;
}
.ant-modal-close {
  cursor: pointer;
  border: 0;
  background: transparent;
  position: absolute;
  right: 18px;
  top: 16px;
  z-index: 10;
  font-weight: 700;
  line-height: 1;
  text-decoration: none;
  -webkit-transition: color .3s ease;
  -o-transition: color .3s ease;
  transition: color .3s ease;
  color: #999;
  outline: 0;
}
.ant-modal-close-x {
  display: block;
  font-style: normal;
  vertical-align: baseline;
  text-align: center;
  text-transform: none;
  text-rendering: auto;
  width: 14px;
  height: 14px;
  font-size: 14px;
  line-height: 1;
}
.ant-modal-close-x:before {
  content: "\e62d";
  display: block;
  font-family: "anticon" !important;
}
.ant-modal-close:focus,
.ant-modal-close:hover {
  color: #444;
  text-decoration: none;
}
.ant-modal-header {
  padding: 14px 16px;
  border-radius: 6px 6px 0 0;
  background: #fff;
  color: #666;
  border-bottom: 1px solid #e9e9e9;
}
.ant-modal-body {
  padding: 16px;
  font-size: 12px;
  line-height: normal;
}
.ant-modal-footer {
  border-top: 1px solid #e9e9e9;
  padding: 10px 18px 10px 10px;
  text-align: right;
  border-radius: 0 0 6px 6px;
}
.ant-modal-footer button + button {
  margin-left: 8px;
  margin-bottom: 0;
}
.ant-modal.zoom-enter,
.ant-modal.zoom-appear {
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
  opacity: 0;
}
.ant-modal-mask {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #373737;
  background-color: rgba(55, 55, 55, 0.6);
  height: 100%;
  z-index: 1050;
  filter: alpha(opacity=50);
}
.ant-modal-mask-hidden {
  display: none;
}
.ant-modal-open {
  overflow: hidden;
}
@media (max-width: 768px) {
  .ant-modal {
    width: auto !important;
    margin: 10px;
  }
  .vertical-center-modal .ant-modal {
    -ms-flex: 1 1;
        flex: 1 1;
  }
}
.ant-confirm .ant-modal-header {
  display: none;
}
.ant-confirm .ant-modal-body {
  padding: 0;
}
.ant-confirm-body .ant-confirm-header {
  background-color: #f9f9f9;
  position: relative;
  height: 50px;
  line-height: 50px;
  border-radius: 6px 6px 0 0;
  padding-left: 15px;
  border-bottom: 1px solid #d2d2d2;
}
.ant-confirm-body .ant-confirm-header .anticon-cross {
  position: absolute;
  right: 13px;
  top: 16px;
  font-size: 15px;
  cursor: pointer;
}
.ant-confirm-body .ant-confirm-header .ant-confirm-header-icon {
  font-size: 24px;
  margin-right: 10px;
  padding: 0 1px;
  position: relative;
  top: 5px;
}
.ant-confirm-body .ant-confirm-title {
  color: #000;
  font-weight: normal;
  font-size: 14px;
}
.ant-confirm-body .ant-confirm-content {
  font-size: 14px;
  color: #000;
  padding: 30px 50px;
}
.ant-confirm .ant-confirm-btns {
  width: 100%;
  border-top: 1px solid #d2d2d2;
  text-align: right;
  padding-right: 15px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.ant-confirm .ant-confirm-btns .ant-btn {
  border-radius: 3px;
  font-size: 12px;
}
.ant-confirm .ant-confirm-btns button + button {
  margin-left: 10px;
  margin-bottom: 0;
}
.ant-confirm-error .ant-confirm-body .ant-confirm-header-icon {
  color: #f50;
}
.ant-confirm-warning .ant-confirm-body .ant-confirm-header-icon,
.ant-confirm-confirm .ant-confirm-body .ant-confirm-header-icon {
  color: #fa0;
}
.ant-confirm-info .ant-confirm-body .ant-confirm-header-icon {
  color: #2db7f5;
}
.ant-confirm-success .ant-confirm-body .ant-confirm-header-icon {
  color: #87d068;
}



