.ant-checkbox {
  white-space: nowrap;
  cursor: pointer;
  outline: none;
  display: inline-block;
  line-height: 1;
  position: relative;
  vertical-align: middle;
}
.ant-checkbox:hover .ant-checkbox-inner,
.ant-checkbox-focused .ant-checkbox-inner {
  border-color: #bcbcbc;
}
.ant-checkbox-inner {
  display: inline-block;
  width: 14px;
  height: 14px;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url('/cloudstore/resource/pc/com/images/checkbox_normal.png');
}
.ant-checkbox-input {
  position: absolute;
  left: 0;
  z-index: 1;
  cursor: pointer;
  opacity: 0;
  filter: alpha(opacity=0);
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
.ant-checkbox-indeterminate .ant-checkbox-inner {
  background-image: url('/cloudstore/resource/pc/com/images/checkbox_indeterminate.png');
}
.ant-checkbox-checked .ant-checkbox-inner {
  background-image: url('/cloudstore/resource/pc/com/images/checkbox_checked.png');
}
.ant-checkbox-disabled.ant-checkbox-checked .ant-checkbox-inner {
  background-image: url('/cloudstore/resource/pc/com/images/checkbox_disable_checked_.png');
}
.ant-checkbox-disabled .ant-checkbox-inner {
  background-image: url('/cloudstore/resource/pc/com/images/checkbox_disable.png');
}
.ant-checkbox-disabled .ant-checkbox-inner-input {
  cursor: default;
}
.ant-checkbox-disabled + span {
  color: #ccc;
  cursor: not-allowed;
}
.ant-checkbox-wrapper {
  cursor: pointer;
  font-size: 12px;
  display: inline-block;
}
.ant-checkbox-wrapper + .ant-checkbox-wrapper {
  margin-left: 8px;
}
.ant-checkbox-wrapper + span,
.ant-checkbox + span {
  margin-left: 6px;
  margin-right: 16px;
}
.ant-checkbox-group {
  font-size: 12px;
}
.ant-checkbox-group-item {
  display: inline-block;
}
@media \0screen {
  .ant-checkbox-checked .ant-checkbox-inner:before,
  .ant-checkbox-checked .ant-checkbox-inner:after {
    font-family: 'anticon';
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\e62e";
    font-weight: bold;
    font-size: 8px;
    border: 0;
    color: #fff;
    left: 2px;
    top: 3px;
    position: absolute;
  }
}



